import React, { useState } from 'react';
import "./App.css";
import { Todolist } from "./Component/To-do-list/To-Do-List";
import { FilterValuesType, TaskStateType } from './Component/Common/Types/TaskType';
import { v1 } from 'uuid';
import { TodolistType } from './Component/Common/Types/TaskType';
import { AddItemForm } from './Component/Common/AddItemForm';


function App() {

  function removeTask(id: string, todolistId: string) {
    let tasks = tasksObj[todolistId]
    let filteredTasks = tasks.filter((t) => t.id !== id)
    tasksObj[todolistId] = filteredTasks
    setTasks({ ...tasksObj })
  }

  function addItem(title: string, todolistId: string) {
    let task = { id: v1(), title: title, isDone: false }
    let tasks = tasksObj[todolistId]
    let newTasks = [task, ...tasks]
    tasksObj[todolistId] = newTasks
    setTasks({ ...tasksObj })
  }

  function changeStatus(taskId: string, isDone: boolean, todolistId: string) {
    let tasks = tasksObj[todolistId]
    let task = tasks.find(t => t.id === taskId)
    if (task) {
      task.isDone = isDone
      setTasks({ ...tasksObj })
    }
  }

  function changeTaskTitle(id: string, newTitle: string, todolistId: string) {
    let tasks = tasksObj[todolistId]
    let task = tasks.find(t => t.id === id)
    if (task) {
      task.title = newTitle
      setTasks({ ...tasksObj })
    }
  }

  function changeTodolistTitle(id: string, newTitle: string) {
    const todolist = todolists.find(tl => tl.id === id)
    if (todolist) {
      todolist.title = newTitle
      setTodolists([...todolists])
    }

  }

  function changeFilter(value: FilterValuesType, todolistId: string) {
    let todolist = todolists.find(tl => tl.id === todolistId)
    if (todolist) {
      todolist.filter = value
      setTodolists([...todolists])
    }
  }

  function addTodolist(title: string) {
    let todolist: TodolistType = {
      id: v1(),
      filter: "all",
      title: title

    }
    setTodolists([todolist, ...todolists])
    setTasks({
      ...tasksObj,
      [todolist.id]: []
    })
  }

  let todolistId1 = v1()
  let todolistId2 = v1()

  let [todolists, setTodolists] = useState<Array<TodolistType>>([
    { id: todolistId1, title: "What to learn", filter: "all" },
    { id: todolistId2, title: "What to buy", filter: "all" }
  ])

  let [tasksObj, setTasks] = useState<TaskStateType>({
    [todolistId1]: [
      { id: v1(), title: "CSS&HTML", isDone: true },
      { id: v1(), title: "JS", isDone: true },
      { id: v1(), title: "React", isDone: false }],
    [todolistId2]: [
      { id: v1(), title: "Book", isDone: false },
      { id: v1(), title: "Phone", isDone: true },
      { id: v1(), title: "Milk", isDone: true }]
  })

  let removeTodolist = (todolistId: string) => {
    let filtredTodolist = todolists.filter(tl => tl.id !== todolistId)
    setTodolists(filtredTodolist)
    delete tasksObj[todolistId]
    setTasks({ ...tasksObj })
  }

  return (
    <div className="App">
   <AddItemForm addItem={addTodolist} />
 
 
     
      {
        todolists.map((tl) => {
          let tasksForToDoList = tasksObj[tl.id];
          if (tl.filter === "completed") {
            tasksForToDoList = tasksForToDoList.filter(t => t.isDone === true)
          }
          if (tl.filter === "active") {
            tasksForToDoList = tasksForToDoList.filter(t => t.isDone === false)
          }
          return ( 
              <Todolist
              key={tl.id}
              id={tl.id}
              title={tl.title}
              tasks={tasksForToDoList}
              removeTask={removeTask}
              changeFilter={changeFilter}
              addTask={addItem}
              changeStatus={changeStatus}
              filter={tl.filter}
              removeTodolist={removeTodolist}
              changeTaskTitle={changeTaskTitle}
              changeTodolistTitle={changeTodolistTitle}
            />
          )
        })
      }
    </div>
  );
}

export default App
